import React from 'react';
import { RootStateOrAny, useSelector } from 'react-redux';

import './Themes.css';

// ?  components
import DashboardDrawer from 'library/common/components/DashboardDrawer/DashboardDrawer';
import BreadCrumb from 'library/common/components/BreadCrumb/BreadCrumb';
import AccountMenu from 'library/common/components/AccountMenu/AccountMenu';
import ThemeCard from './components/ThemeCard/ThemeCard';

const Themes = () => {
  const { loggedInUser } = useSelector((state: RootStateOrAny) => state.login);
  console.log(loggedInUser);
  if (loggedInUser != null) {
    console.log(loggedInUser);

    return (
      <main>
        <DashboardDrawer />

        <section className="home-section">
          <div>
            <AccountMenu />
            <header className="text">Themes</header>
            <BreadCrumb currentPath="payment" />
            <div className="theme__cardContainer">
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
              <ThemeCard />
            </div>
          </div>
        </section>
      </main>
    );
  }
  return <div className="display-3 text-center mt-5">Not logged in </div>;
};

export default Themes;
