import React from 'react';
import styled from 'styled-components';

const ThemeCardSmall = styled.div`
  width: 30rem;
  cursor: pointer;
  margin: 1rem;
  font-size: 1.4rem;
  overflow: hidden;
  border-radius: 0.35rem;
  background-color: rgb(17, 16, 29);
  /* border: 0.25rem solid black; */

  @media only screen and (max-width: 840px) {
    width: 60%;
  }
  @media only screen and (max-width: 640px) {
    width: 75%;
  }
  @media screen and (min-width: 1900px) and (max-width: 5000px) {
    width: 30rem;
  }

  &:hover {
    /* border: 0.25rem solid green; */
  }
`;

const ThemeCardImg = styled.img`
  width: 100%;
`;
const PreviewBtn = styled.button`
  padding: 0.75rem 2rem;
  border: 0.25rem solid black;
  border-radius: 0.5rem;
  font-size: 1.8rem;
  cursor: pointer;
  background-color: rgb(228, 233, 247);
  &:hover {
    color: white;
    border: 0.25rem solid white;
    background-color: rgb(17, 16, 29);
  }
`;
const UseBtn = styled.button`
  padding: 0.75rem 2rem;
  border: 0.25rem solid black;
  border-radius: 0.5rem;
  font-size: 1.8rem;
  cursor: pointer;
  background-color: rgb(228, 233, 247);
  &:hover {
    color: white;
    border: 0.25rem solid white;
    background-color: rgb(17, 16, 29);
  }
`;
const themeCardBtnContainer = {
  display: 'flex',
  justifyContent: 'center',
  margin: '0.5rem 0rem',
};
const ThemeCard = () => {
  return (
    <ThemeCardSmall>
      <ThemeCardImg
        src="https://cdn.dribbble.com/users/1527694/screenshots/11099332/personal_2x_4x.png"
        alt=""
      />

      <div className="themeCard__btnContainer" style={themeCardBtnContainer}>
        <PreviewBtn className="medium" type="button">
          Preview
        </PreviewBtn>
        <UseBtn className="medium" type="button">
          Use
        </UseBtn>
      </div>
    </ThemeCardSmall>
  );
};

export default ThemeCard;
